.game {
    max-width: 500px;
    margin: 0 auto;
  }

  .score {
    text-align: center;
  }
  
  .body {
    display: -ms-flexbox;
    display: flex;
  }
  
  .help {
    color: #666;
    margin: 10px;
    text-align: center;
  }
  
  .stars {
    width: 50%;
    text-align: center;
    border: thin solid lightgray;
  }
  
  .play-numbers {
    width: 50%;
    text-align: center;
    border: thin solid lightgray;
    padding: 10px;
  }
  
  .star {
      display: inline-block;
    margin: 0 15px;
  }
  
  .star:after {
    content: "\2605";
    font-size: 45px;
  }
  
  .number {
    background-color: #eee;
    border: thin solid #ddd;
    width: 45px;
    height: 45px;
    margin: 10px;
    font-size: 25px;
  }
  
  .play-number:focus, play-number:active {
    outline: none;
    border: thin solid #ddd;
  }
  
  .game-done .message {
    font-size: 250%;
    color: green;
    font-weight: bold;
    margin: 15px;
  }


